:root {
  --bc: #d9d9d9;
  --blue: #4484ce;
  --darkblue: #06111f;
  --yellow: #f9cf00;
  --orange: #f19f4d;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Arial Narrow", Arial, sans-serif;
}
::-webkit-scrollbar{
  height: 12px;
  width: 12px;
  }
  
  ::-webkit-scrollbar-track{
  background: rgba(255, 255, 255, 0.2);
  border-radius: 1ex;
  }
  
  ::-webkit-scrollbar-thumb{
  background: skyblue;
  border-radius: 1ex;
  }
body {
  background-color: var(--blue);
}
.App {
  max-width: 1300px;
  margin: auto;
}
.bars {
  display: none;
}
.headercontainer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px;
  height: 80px;
}
.logo {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
}
.logo img {
  width: 50px;
  height: 35px;
}
ul {
  list-style: none;
}

.right {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
}
.menu {
  display: flex;
}
.menu li {
  padding: 10px;
  text-transform: uppercase;
}
.right input {
  padding: 10px;
  background-color: var(--orange);
  border-radius: 5px;
  outline: none;
  border: none;
  color: var(--grey);
}
.cart {
  width: 30px;
  height: 30px;
}
.menu li:hover,
.cart:hover {
  cursor: pointer;
  color: var(--orange);
}

.herocontainer {
  width: 80%;
  height: 100%;
  margin: auto;
  display: grid;
  grid-template-columns: 1fr 3fr 1fr;
  justify-content: center;
  justify-items: center;
  text-align: center;
  margin-top: 20px;
}
.text1,
.text2,
.traffic,
.customers {
  font-size: 1.5rem;
  text-transform: capitalize;
}
.text2 > :first-child,
.text1 > :nth-child(2),
.customers > :nth-child(2),
.traffic > :first-child {
  color: var(--orange);
}
.h_side,
.text2 {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
.wrapper {
  position: relative;
}
.bluecircle {
  width: 400px;
  height: 400px;
  border-radius: 50%;
  /* opacity: 0.8; */
  background-color: var(--orange);
}

.wrapper img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}

.cart2 {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  margin-top: 20px;
  text-transform: uppercase;
  /* width: 100px; */
}
.signup {
  width: 100%;
  display: flex;
  /* flex-direction: column; */
  justify-content: center;
  align-items: center;
}
.cart2 div {
  padding: 5px;
  background-color: var(--darkblue);
  border-radius: 5px;
  color: white;
}

.cart2 div:hover {
  cursor: pointer;
}

.virtual {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 50px auto;
}
.vleft {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.vleft > :first-child {
  color: var(--orange);
  font-size: 2rem;
  font-weight: bold;
  text-transform: uppercase;
  margin-bottom: 10px;
}
.vleft > :nth-child(3) {
  margin-top: 20px;
}
.vleft img {
  width: 30%;
}
.vwrapper {
  width: 400px;
}

.pcontainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 20px;
  margin-bottom: 20px;
}
.phead {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
}
.phead img {
  width: 100px;
  height: 100px;
  border-radius: 50%;
}
/* .phead img{
  width: 100%;
} */
.pmenu li {
  text-align: center;
  width: 100%;
  padding: 10px;
  text-transform: uppercase;
}
.pmenu li:hover {
  cursor: pointer;
  color: var(--bc);
  background-color: #fff;
}
.products {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  gap: 10px;
}
.plist {
  display: flex;
  flex-wrap: wrap;
  width: 80%;
  gap: 10px;
  justify-content: center;
  height: 400px;
  overflow-y: scroll;
}
.plist .product {
  width: 200px;
  height: 150px;
  padding: 10px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  background-color: white;
  color: var(--darkblue);
  border-radius: 10px;
  overflow: hidden;
  position: relative;
}
.pname {
  display: flex;
  flex-direction: column;
}
.pname > :first-child {
  font-weight: bold;
  color: var(--orange);
}

.product img {
  width: 30%;
  position: absolute;
  /* transform: rotate(-20deg); */
  right: 0;
  height: 100%;
  bottom: -20px;
}

.footerwrapper {
  width: 100%;
  height: 100%;
}

.cfooter {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  margin: 20px;
}
.flogo {
  display: flex;
  align-items: center;
  gap: 10px;
}
.flogo img {
  width: 35px;
  height: 35px;
}

.block .detail {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
}

.detail > :first-child {
  text-transform: uppercase;
  font-size: 1rem;
  margin-bottom: 10px;
}
.pngLine {
  opacity: 0.8;
  font-size: 0.8rem;
  display:flex;
  justify-content: center;
  align-items: center;
}

.copy {
  width: 100%;
  display: flex;
  flex-direction: column;
  text-align: center;
  opacity: 0.6;
}

@media screen and (max-width: 768px) {
  .bars {
    display: block;
  }
  .herocontainer {
    grid-template-columns: 1fr 3fr;
  }
  .r_side {
    grid-column: 1/3;
    display: flex;
    flex-direction: row;
    /* gap: 20px; */
    width: 100%;
    margin: 20px auto;
  }
  .right input {
    display: none;
  }

  .virtual {
    flex-direction: column;
  }
  .vleft > :nth-child(2),
  .vleft img {
    display: none;
  }
  .vleft > :nth-child(3) {
    /* align-self: flex-start; */
    margin-top: 0;
    margin-bottom: 10px;
  }
  .products {
    flex-direction: column;
    width: 100%;
  }
  .pmenu {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
  }
  .plist {
    width: 100%;
  }
  .cfooter {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 20px;
  }
  .flogo {
    grid-row: 1/3;
  }
  .phead {
    text-align: center;
  }
}

@media screen and (max-width: 550px) {
  .menu {
    display: flex;
    flex-direction: column;
    width: 80%;
    height: 100%;
    margin-top: 110px;
    z-index: 99;
    background-color: #fff;
    border-radius: 10px;
  }

  .herocontainer {
    grid-template-columns: 1fr;
    gap: 20px;
  }
  .bluecircle {
    width: 300px;
    height: 300px;
  }
  .pcontainer {
    width: 80%;
    margin: auto;
  }

  .text2 {
    display: none;
  }
  .plist .product {
    width: 150px;
  }
  .r_side {
    grid-column: auto;
  }
  .vwrapper {
    width: 300px;
  }
}
@media screen and (max-width: 450px) {
  .cfooter {
    grid-template-columns: 1fr 2fr;
    justify-content: center;
    align-items: center;
  }
  .bluecircle {
    width: 200px;
    height: 200px;
  }
  .flogo {
    grid-row: 1/5;
  }
}
